import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, AlertController } from 'ionic-angular';

import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-configure',
  templateUrl: 'configure.html',
})
export class ConfigurePage {
  masterArray: any;
  itemList: any;
  page: string;
  reorder: any = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public alertCtrl: AlertController, public events: Events) {
    this.masterArray = navParams.get('masterArray');
    this.itemList = navParams.get('itemList');
    this.page = navParams.get('page');
  }

  sortItems() {
    this.reorder = !this.reorder;
  }

  reorderItems(indexes) {
    let element = this.itemList[indexes.from];
    this.itemList.splice(indexes.from, 1);
    this.itemList.splice(indexes.to, 0, element);
    this.save();
  }

  addItem() {
    const prompt = this.alertCtrl.create({
      title: 'New ' + this.page + ' item',
      message: "Enter a new name to add to this list.",
      inputs: [
        {
          name: 'title',
          placeholder: 'New item'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            //console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            if(data.title){
              if(this.page === 'Location') {
                this.itemList.push({
                  title: data.title,
                  count: 0,
                  display: true,
                  selected: false,
                  id: this.itemList.length,
                  categoryList: []
                });
              } else if(this.page === 'Category') {
                this.itemList.push({
                  title: data.title,
                  count: 0,
                  display: true,
                  id: this.itemList.length,
                  itemList: []
                });
              } else {
                this.itemList.push({
                  title: data.title,
                  count: 0,
                  id: this.itemList.length,
                  display: true
                });
              }
            }
          }
        }
      ]
    });
    prompt.present();
  }

  editItem() {

  }

  deleteItem(item) {
    this.itemList.splice(item.id, 1);
    if(item.selected === true) {
      this.itemList[0].selected = true;
      this.masterArray.currentLocation.title = this.itemList[0].title;
      this.masterArray.currentLocation.id = this.itemList[0].id;

      // Save to localStorage and set event to update HomePage
      // this.save();
      console.log(this.masterArray);
      this.event();
    }
  }

  event(){
    this.events.publish('item:updated', this.masterArray);
  }

  save(){
    this.storage.set('masterArray', this.masterArray); // Save 'masterArray' into storage
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ConfigurePage');
  }

}



